// * Lib
import React, { Component, createRef, useEffect } from 'react'
import Parallax from 'parallax-js'

// * Utils
import { gifs, images } from '@constants'
// * Styles
import './styles.scss'

// * Display/UI
import { Envelope, Button, Confetti } from '@atoms'
import video from './butterfly.webm'

interface IState {
   throwConfetti: boolean
}

class Love extends Component<{}, IState> {
   public state: Readonly<IState> = {
      throwConfetti: false,
   }

   private envelopeRef = createRef<HTMLDivElement>()
   scene: HTMLElement | null = {} as HTMLElement
   parallaxInstance = createRef()

   componentDidMount = () => {
      this.scene = document.getElementById('love-scene')
      this.parallaxInstance = new Parallax(this.scene)
   }

   onHeartButtonClick = () => {
      const isFocused = this.envelopeRef.current?.getAttribute('data-focused')
      this.envelopeRef.current?.setAttribute(
         'data-focused',
         isFocused == 'false' ? 'true' : 'false',
      )
      this.setState({
         throwConfetti: true,
      })
   }
   render = () => {
      return (
         <div className="love">
            <Confetti throwConfetti={this.state.throwConfetti} />
            <video
               className="love__background"
               muted={true}
               autoPlay
               playsInline
               loop
               id="myVideo"
            >
               <source src={video} type="video/webm" />
               Sorry, your browser doesn't support embedded videos.
            </video>
            <div className="love__envelop-container">
               <Envelope
                  ref={this.envelopeRef}
                  message="Te Amo"
                  destinatary="Para: Mi Pretz"
               >
                  <div className="heart" />
                  <img
                     className="love__balloons-couple"
                     src={gifs.BALLOON_COUPLE}
                  />
               </Envelope>
            </div>
            <div className="love__heart-button">
               <Button onPress={this.onHeartButtonClick} />
            </div>
            <img
               className="love__nori-worried"
               src={gifs.NORI_WORRIED}
               alt=""
            />
            {/* <div className="love__nori" />
            <div className="love__panki" /> */}
            <div id="love-scene"></div>
         </div>
      )
   }
}

export default Love
